* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    }
/*card styling*/
.card{
 margin: 1vh;
  object-fit: cover;
  box-shadow: 0.4px 0.5px 0.5px 1px rgb(215, 223, 223);
  border-radius: 1vh;
  position: relative;
  width: 40vh;
  height: 25vh;
  margin-top:4vh;
}
.card h5{
   font-size: 2.5vh;
   letter-spacing: 0.2vw;
   font-family:'sans-serif';
   }
.image-from-card{
    width: 40vh;
    height: 20vh;
    border-radius: 1vh;
}
.card-listings{
    display: grid;
    grid-template-columns: 1fr;
    grid-column-start: 1;
    justify-items: left;
    overflow-y: scroll;
}
.card-text{
    position: absolute;
    z-index: 1;
    top:5vh;
    left:5vw;
    color:rgb(13, 10, 28);
    opacity: 80%;
    font-size: 1.8vh;
    font-family: 'Times New Roman', Times, serif;
    background-image: linear-gradient(180deg, transparent, transparent,rgb(0,48, 53),transparent,transparent);
    border-radius:1vh;
}
.image-from-card:hover{
    opacity: 60%;
    cursor: pointer;
}
.card .card-text:hover{
    opacity:1;
    cursor: pointer;
}
.card-text h4{
    color: white;
    margin-top:2vh;
    font-size: 2.5vh;
    font-weight:bolder;
}
.hide-text{
    display: none;
}
/* comment card sign up*/
.comment-card{
    text-align: center;
    margin-top: 2vh;
    font-size: 1.2vh;
    color:aliceblue;
    border-radius: 1vw;
    width:20vw;
    height: 10vh;
    box-shadow: 0px 0.1px 0.2px 0.2px rgb(106, 138, 138);
}
.card-text{
    position: absolute;
    visibility: hidden;
}
.comment-card h6{
   
    font-size: 2vh;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin-left: -20vh;
}
.comment-card p{
    font-size: 2vh;
    font-family: 'Times New Roman', Times, serif;
    margin-top: 0.5vh;
    margin-left: -8vw;
}
.comment-card small{
    font-size: 1.6vh;
    font-family: Arial, Helvetica, sans-serif;
    margin-left: -12vh;
}

/*card styling*/
@media (max-width:600px){
    .card{
        aspect-ratio: 1/1;
         object-fit: cover;
         box-shadow: 0px 0.1px 0.2px 0.4px rgb(215, 223, 223);
         width:41vw;
        height:18vh;
       }
    .card-listings{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 1vw;
    }
    .card-listings div{
        display: grid;
        grid-template-columns: 100px 100px;
        grid-template-rows: auto;
    }
    .card h5{
        font-size: 1.4vh;
        letter-spacing: 0.1vw;
        font-family:'sans-serif';
        }
     .image-from-card{
         width: 40vw;
         height:15vh;
         margin-left: -25vw;
         margin-top: 2vh;
     }
     .card-text{
        position: absolute;
        display: none;
    }
     .comment-card p{
        font-size: 1.5vh;
        margin-top: 0.2vh;
        margin-left: -30vw;
    }
    .comment-card h6{
        font-size: 2vh;
        margin-left: -10vh;
    }
    .comment-card small{
        font-size: 1.2vh;
        margin-left: -8vh;
    }
    .comment-card{
        width:50vw;
        height: 7vh;
    }
}
@media only screen and (min-width: 624px) and (max-width: 1024px){
    .card{
        aspect-ratio: 1/1;
