        width:41vw;
       height:18vh;
      }
      .card-listings{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 1vw;
    }
    .card-listings div{
        display: grid;
        grid-template-columns: 100px 100px;
        grid-template-rows: auto;
    }
   .card h5{
       font-size: 1.4vh;
       letter-spacing: 0.1vw;
       font-family:'sans-serif';
       }
       .image-from-card{
        width: 40vw;
        height:15vh;
        margin-left: -12vw;
        margin-top: 2vh;
    }
    .comment-card p{
        font-size: 1.5vh;
        margin-top: 0.2vh;
        margin-left: -30vw;
    }
    .comment-card h6{
        font-size: 2vh;
        margin-left: -20vh;
    }
    .comment-card small{
        font-size: 1.2vh;
        margin-left: -15vh;
    }
    .comment-card{
        width:50vw;
        height: 7vh;
    }
}